import styled from 'styled-components';
import {Gutter, header_height} from './variables';

const PageWrapper = styled.div` 
    padding: ${props => props.padding ? props.padding : `${header_height} 0 0`} } 
    background-color: ${props => props.background && props.background};
    min-height: 100%;
    position: relative;
`;

export {PageWrapper};


