import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {HBox, VBox, Element} from 'react-stylesheet';

// import DataStore from '../flux/stores/DataStore.js'
import {Container, FontSize, Gutter, Zindex} from '../style/style';
import ShareIcons from "./utils/ShareIcons";
import OurPartners from "./utils/OurPartners";

class Footer extends React.Component {
    render() {
        return (
            <SiteFooter id="site-footer" className="site-footer">
                <Container>
                    <HBox justifyContent={'space-between'}
                          alignItems={'center'}
                          flexWrap={'wrap'}>

                        <ShareIcons size={25} color={'#fff'}/>

                        <OurPartners/>

                    </HBox>
                </Container>


            </SiteFooter>
        );
    }
}

export default Footer;

const SiteFooter = styled.footer`
        background-color: rgba(0,0,0,0.9);
        padding: ${Gutter.sm} 0;
        z-index: ${Zindex.modal};
    `;
const

    DotsLogo = styled.div`
    display: flex;
    .dot {
        width: 1.2rem;
        height: 1.2rem;
        background-color: #fff;
        border-radius: 50%;
        margin: .3rem;
    }
    `;


