import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {HBox, VBox, Element} from 'react-stylesheet';
import IoIosCartOutline from 'react-icons/lib/io/ios-cart-outline';

// import DataStore from '../flux/stores/DataStore.js'
import {Container, FontSize, Gutter, Zindex} from '../style/style';
import {header_height} from "../style/variables";


class Header extends React.Component {
    render() {
        return (
            <SiteHeader id="site-header" className="site-header">
                <Container>
                    <HBox justifyContent={'space-between'} alignItems={'center'}>
                        <Element>
                            <MenuIcon>
                                <span className={'line'}></span>
                                <span className={'line'}></span>
                            </MenuIcon>
                        </Element>

                        <Element>
                            <Link to="/" style={{marginRight: '10px'}}>
                                <DotsLogo>
                                    <Element className={'dot'} />
                                    <Element className={'dot'} />
                                    <Element className={'dot'} />
                                </DotsLogo>
                            </Link>
                        </Element>

                        <Element>
                            <IoIosCartOutline color={'#fff'} size={40}/>
                        </Element>
                    </HBox>
                </Container>
                {/*{allPages.map((page) => {*/}
                {/*if(page.slug != 'home'){*/}
                {/*return(*/}
                {/*<Link */}
                {/*key={page.id} */}
                {/*to={`/${page.slug}`}*/}
                {/*style={{marginRight: '10px'}}*/}
                {/*>*/}
                {/*{page.title.rendered}*/}
                {/*</Link>*/}
                {/*)                     */}
                {/*}*/}
                {/*})}*/}

            </SiteHeader>
        );
    }
}

export default Header;

const SiteHeader = styled.header`
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.9);
    padding: ${Gutter.sm} 0;
    z-index: ${Zindex.modal};
    height: ${header_height};
`;

const MenuIcon = styled.div`
    width: 4rem;
    height: 4rem;
    border: .2rem solid #fff;
    border-radius: 50%;
    padding: 1.2rem 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    cursor: pointer;
    transform-origin: center center;
    transition: transform .3s;
    .line { 
        height: .2rem;
        width: 100%;
        background-color: #fff;
        position: relative;
    }
    &:hover {
        transform: scale(.9)
    }`
    ,

    DotsLogo = styled.div`
        display: flex;
        .dot {
            width: 1.2rem;
            height: 1.2rem;
            background-color: #fff;
            border-radius: 50%;
            margin: .3rem;
        }
    `;


