import React from 'react';
import styled from 'styled-components';
import {HBox, VBox, Element} from 'react-stylesheet';
import DataStore from '../flux/stores/DataStore.js'
import {PageWrapper, JumboTitle, ResponsiveHalf, Gutter, FontFamily} from '../style/style';

class About extends React.Component {
    render() {
        const {homepage} = this.props,
            pageData = DataStore.getPageBySlug('about'),
            featuredImage = pageData._embedded['wp:featuredmedia']['0']['source_url'];

        return (
            <Element className={'about-us'} background={'#cdcdcd'}>
                <HBox flexWrap={'wrap'}>
                    <ResponsiveHalf>
                        <VBox justifyContent={'center'} alignItems={'center'} height={'100%'}>
                            <JumboTitle color={'#000'}>{pageData.title.rendered}</JumboTitle>
                            <div style={{fontSize: '2rem', fontFamily: FontFamily.content}}
                                 dangerouslySetInnerHTML={{__html: pageData.content.rendered}}/>
                        </VBox>
                    </ResponsiveHalf>

                    <ResponsiveHalf padding={Gutter.md}>
                        <img src={featuredImage}/>
                    </ResponsiveHalf>
                </HBox>
            </Element>
        );
    }
}

export default About;

const AboutPage = styled.div`
    border-bottom: 2rem solid #fff;
`;


