import React from 'react';
import styled from 'styled-components';
import DataStore from '../flux/stores/DataStore.js'
import {HBox, VBox, Element} from 'react-stylesheet';
import {PageWrapper, JumboTitle, ResponsiveHalf, FontFamily, FontSize, Gutter} from '../style/style';

class Team extends React.Component {
    render() {
        const {homepage} = this.props,
            pageData = DataStore.getPageBySlug('team'),
            members = [pageData.acf.member_1, pageData.acf.member_2];

        console.log('members', members);
        return (
            <PageWrapper className={'team'}
                         background={'#f5f5f5'}
                         padding={homepage && '5rem'}>
                <JumboTitle color={'#000'}
                            responsive>
                    {pageData.title.rendered}
                </JumboTitle>

                {/*<div dangerouslySetInnerHTML={{__html: pageData.content.rendered}}/>*/}

                {members.map((member, i) => {
                    const {name, content, image} =  member;
                    return (
                        <Member key={i}>
                            <HBox className={'member'}>
                                <Element>
                                    <img src={image}
                                         width={'100px'}
                                         className={'member_image'}/>
                                </Element>
                                <Element>
                                    <h4 className={'member_name'}>{name}</h4>
                                    <p className={'member_content'}>{content}</p>
                                </Element>
                            </HBox>
                        </Member>

                    )
                })}

            </PageWrapper>
        );
    }
}

const Member = styled.div`
    margin-bottom: ${Gutter.sm};
    .member {
        &_name {
            font-family: ${FontFamily.title};
            font-size: ${FontSize.sm};
            margin: 0;
        }
        &_content {
            font-family: ${FontFamily.content};
            font-size: ${FontSize.sm};
            margin: 0;
        }
        &_image {
        
        }
    }
`;


export default Team;



