import React from 'react';
import {HBox, VBox, Element} from 'react-stylesheet';
import styled from 'styled-components';

import FaFacebook from 'react-icons/lib/fa/facebook';
import FaInstagram from 'react-icons/lib/fa/instagram';
import FaTwitter from 'react-icons/lib/fa/twitter';
import {Gutter} from "../../style/style";

class ShareIcons extends React.Component {
    render() {
        const {size, color} = this.props;
        return (
            <HBox>
                <ShareIcon>
                    <FaInstagram color={color ? color : '#fff'} size={size ? size : 20}/>
                </ShareIcon>
                <ShareIcon>
                    <FaFacebook color={'#fff'} size={size ? size : 20}/>
                </ShareIcon>
                <ShareIcon>
                    <FaTwitter color={'#fff'} size={size ? size : 20}/>
                </ShareIcon>
            </HBox>
        )
    }
}

export default ShareIcons;


const ShareIcon = styled.div`
        margin: 0 ${Gutter.xxs};
        cursor: pointer;
    `;


