import React from 'react';
import DataStore from '../flux/stores/DataStore.js'
import Archive from "./Archive";
import {Gutter, PageWrapper, ResponsiveHalf} from '../style/style';
import About from "./About";
import {HBox, VBox, Element} from 'react-stylesheet';
import Contact from "./Contact";
import Team from "./Team";
import Footer from "./Footer";


class Home extends React.Component {
    render() {
        const pageData = DataStore.getPageBySlug('home');


        return (
            <PageWrapper className={'homepage'}>
                {/*<h2>Homepage template</h2>*/}
                {/*<h1>{pageData.title.rendered}</h1>*/}

                {/*<div dangerouslySetInnerHTML={{__html: pageData.excerpt.rendered}} />*/}
                {/*<div>{pageData.text}</div>*/}
                <Archive archiveType={'products'}/>

                <About homepage/>

                <HBox flexWrap={'wrap'}>
                    <ResponsiveHalf padding={`${Gutter.sm} ${Gutter.xs} ${Gutter.sm} ${Gutter.sm}`}>
                        <Contact homepage/>
                    </ResponsiveHalf>
                    <ResponsiveHalf padding={`${Gutter.sm} ${Gutter.sm} ${Gutter.sm} ${Gutter.xs}`}>
                        <Team homepage/>
                    </ResponsiveHalf>
                </HBox>

                <Footer/>

            </PageWrapper>
        );
    }
}

export default Home;



