import React from 'react';
import styled from 'styled-components';
import DataStore from '../flux/stores/DataStore.js'
import {PageWrapper, JumboTitle, Colors} from '../style/style';

class Contact extends React.Component {
    render() {
        const {homepage} = this.props,
            pageData = DataStore.getPageBySlug('contact');

        return (
            <PageWrapper className={'contact-us'}
                         background={'#f5f5f5'}
                         padding={homepage && '5rem'}>

                <JumboTitle color={'#000'}
                            responsive>
                    {pageData.title.rendered}
                    </JumboTitle>

                <div style={contactDetails} dangerouslySetInnerHTML={{__html: pageData.content.rendered}}/>
                <div>{pageData.text}</div>
            </PageWrapper>
        );
    }
}

export default Contact;

const contactDetails = {
    fontSize: '3rem',
    fontWeight: 'bold',
    lineHeight: '4rem'
};



